.mapover {
  margin: 0;
  padding: 0;
  position: absolute;
  right: 10px;
  bottom: 20px;
  z-index: 9999;
  width: 150px;
  text-align: center;
  color: salmon;
  font-weight: 600;
}
.mapover ul li {
  border-radius: 7px;
}
.mapover ul li:nth-child(4) {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: gray;
}
.mapover ul li:nth-child(3) {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: red;
}
.mapover ul li:nth-child(2) {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: yellow;
}
.mapover ul li:nth-child(1) {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: green;
}

.position {
  position: relative;
}
.position > .icon {
  font-size: 50px;
  color: white;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  position: absolute;
  bottom: 20px;
  left: 20px;
  z-index: 9999;
  cursor: pointer;
}
